<template>
    <div id='gymbranch' class="addprivatekind">
        <div class='top'>
          <span class='fundInfo' @click='goback()'>私教种类</span>
          <img src='../../assets/images/icon_nav_arrow.png'/>
          <span class='branch'>{{text}}</span>
        </div>
        <el-form ref="form" :model="form" :rules='rules'>
            <el-form-item label="私教课类型: " prop="type" class='formNew flexContent' style="border: 0; background: none; padding:0">
              <el-radio-group v-model="form.type" size="small" v-if="modify">
                <el-radio-button label="PRIVATE" v-show="form.type == 'PRIVATE'">
                  <img src="../../assets/svg/icon_select.svg"/>
                  私教课</el-radio-button>
                <el-radio-button label="SPECIAL" v-show="form.type == 'SPECIAL'">
                  <img src="../../assets/svg/icon_select.svg"/>
                  特色课</el-radio-button>
              </el-radio-group>
              <el-radio-group v-model="form.type" size="small" v-if="!modify">
                <el-radio-button label="PRIVATE">
                  <img src="../../assets/svg/icon_select.svg" v-show="form.type == 'PRIVATE'"/>
                  私教课</el-radio-button>
                <el-radio-button label="SPECIAL">
                  <img src="../../assets/svg/icon_select.svg" v-show="form.type == 'SPECIAL'"/>
                  特色课</el-radio-button>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="课程种类名：" class='formNew' prop='courseName' :class="{inputFocus: inputFocus == '课程种类'}">
                <el-input v-model="form.courseName" class='inputNew' placeholder="请输入课程种类" @focus="focus('课程种类')" @blur="blur()" maxlength = '15'></el-input>
            </el-form-item>
            <el-form-item label="课程介绍：" class='formNew noPos' :class="{inputFocus: inputFocus == '课程介绍'}">
                <el-input
                  type="textarea"
                  :rows="4"
                  placeholder="请输入内容"
                  v-model="form.brief"
                  @focus="focus('课程介绍')"
                  @blur="blur()"
                  maxlength="2999"
                  >
                </el-input>
            </el-form-item>
            <el-form-item label="课程时长（分钟）：" class='formNew' prop='durationTime'>
                <el-input v-model.number="form.durationTime" :readonly="true" class='inputNew'  placeholder="请输入时长" :maxlength="5" @focus="focus('课程时长')" @blur="blur()"></el-input>
            </el-form-item>
            <el-form-item label="可约人数: " class='formNew noBg' prop='canAppointmentPersonNum' :class="{inputFocus: inputFocus == '可约人数'}">
                <el-select v-model="form.canAppointmentPersonNum" placeholder="请选择" class="noBg" @focus="focus('可约人数')" @blur="blur()"> 
                  <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
            </el-form-item>
            <p class="coursePrice">课程价格 (元 / 节) : </p>
            <div class="vipPrice">
              <div class="price1" :class="{inputFocus: inputFocus == '非会员', redBorder: redBorder}">
                <span>非会员: </span>
                <input type="text" @blur="blur3(form.generalPrice,form.vipPrice)" maxlength = '4' :class="{}" v-model="form.generalPrice" @focus="focus('非会员')"/>
                <p v-if="errorTips1" class="errorTip">{{errorTips1}}</p>
              </div>
              <div class="price2" :class="{inputFocus: inputFocus == '会员', redBorder: redBorder}">
                <img src="../../assets/images/icon_vip.png" class="vipimg"/>
                <span>会员: </span>
                <input type="text" @blur="blur3(form.generalPrice,form.vipPrice)" maxlength = '4' v-model="form.vipPrice"  @focus="focus('会员')"/>
                <p v-if="errorTips2" class="errorTip">{{errorTips2}}</p>
              </div>
            </div>
            <div class="payWay">
              <span class="payWayspan">支付方式 :</span>
              <div class="errorTips3" v-if="errorTips3">{{errorTips3}}</div>
              <div class="groupRight">
                  <div v-for="(item, index) in methodList" :label="item.methods" :key="index" class="methodsWrap">
                    <el-checkbox v-model="item.select" @change="handleChecked(item.select, item)" class="methodTitle">{{item.value}}</el-checkbox>
                    <div v-if="item.list.length == 0 && item.select && item.value != '在线支付'" class="nocardList">无可用卡种</div>
                    <div v-if="item.list.length == 0 && item.select && item.value != '在线支付'" class="addcardList">
                      <div class="toAddCard" @click="toaddCard()">
                        <i class="iconfont icon-btn_icon_add"></i>
                        <span>添加</span>
                      </div>
                    </div>
                    <el-checkbox-group v-model="item.selectList" @change="handleCheckedPayment(item.selectList)" v-if="item.select && item.list.length > 0" class="methodPayList">
                      <el-checkbox v-for="(item, index) in item.list" :label="item.code" :key="index">{{item.name}}</el-checkbox>
                    </el-checkbox-group>
                </div>
              </div>
            </div>
            <div class='btn'>
                <div class='cancle' @click="cancle" style="padding-top: 1px">取消</div>
                <div class='submit' @click="submit('form')">确定</div>
            </div>
        </el-form>
    </div>
</template>
<script>
import {addsession, updatesession, vipCardKindList} from '../../services/service.js'
import {isApiResponseOk, getSessionStorage, transDate} from '../../utils/com.js'
export default {
  data () {
    return {
      form: {
        courseName: '', 
        generalPrice: '', 
        vipPrice: '', 
        durationTime: 60, 
        brief: '',
        canAppointmentPersonNum: '',
        type: ''
      },
      rules: {
        courseName: [{required: true, message: '课程种类不能为空', trigger: 'blur'}],
        canAppointmentPersonNum: [{required: true, message: '可约人数不能为空', trigger: 'blur'}, {type: 'number', message: '人数必须为数字类型'}],
        durationTime: [
          {required: true, message: '时长不能为空', trigger: 'blur'}, {type: 'number', message: '时间必须是整数类型'}
        ],
        type: [
          { required: true, message: '请选择私教课种类', trigger: 'blur' }
        ]
      },
      methodList: [
        {methods: 'packagePayList', value: '期限卡支付', list: [], select: false, selectList: []},
        {methods: 'timesPayList', value: '次卡支付', list: [], select: false, selectList: []},
        {methods: 'prepaidPayList', value: '储值卡支付', list: [], select: false, selectList: []},
        {methods: 'otherPayList', value: '在线支付', list: [], select: false, selectList: []}
      ],
      modify: false,
      text: '添加私教种类',
      radio: 'online',
      options: [{
          value: 1,
          label: 1
        }, {
          value: 2,
          label: 2
        }, {
          value: 3,
          label: 3
        },{
          value: 4,
          label: 4
        }, {
          value: 5,
          label: 5
        }],
        errorTips1: '',
        errorTips2: '',
        errorTips3: '',
        inputFocus: '',
        redBorder: false
    }
  },//迭代31第一堂私教课
  watch: {
    // 'form.type': function () {
    //   this.$nextTick(function () {
    //     if (this.form.type == 'SPECIAL') {
    //       this.methodList = [
    //         {methods: 'otherPayList', value: '在线支付', list: [{name:'微信支付', code: 'WXPAY'}], select: false, selectList: []}, 
    //         {methods: 'prepaidPayList', value: '会员支持储值卡', list: [], select: false, selectList: []},
    //         {methods: 'packagePayList', value: '会员包时卡', list: [], select: false, selectList: []}
    //       ]
    //       this.saveData()
    //     } else {
    //       this.methodList = [
    //         {methods: 'otherPayList', value: '在线支付', list: [{name:'微信支付', code: 'WXPAY'}], select: false, selectList: []}, 
    //         {methods: 'prepaidPayList', value: '会员支持储值卡', list: [], select: false, selectList: []}, 
    //         {methods: 'timesPayList', value: '会员私教卡', list: [], select: false, selectList: []},
    //         {methods: 'packagePayList', value: '会员包时卡', list: [], select: false, selectList: []}
    //       ]
    //       this.saveData()
    //     }
    //   })
    // }
  },
  created () {
    this.modify = this.$route.query.modify
    let that = this
    if (this.modify) {
      let detailData = getSessionStorage('detailInfo', {}) // 从之前的页面跳转传过来的数据
      this.form.courseName = detailData.name
      // if (detailData.otherPayList) {
      //   this.form.otherPayList = detailData.otherPayList
      // }
      // if (detailData.packagePayList) {
      //   this.form.packagePayList = detailData.packagePayList
      // }
      // if (detailData.prepaidPayList) {
      //   this.form.prepaidPayList = detailData.prepaidPayList
      // }
      // if (detailData.timesPayList) {
      //   this.form.timesPayList = detailData.timesPayList
      // }
      // this.form.paymentList = detailData.payment     支付方式列表
      this.form.generalPrice = detailData.generalPrice
      this.form.vipPrice = detailData.vipPrice
      this.form.durationTime = detailData.durationTime
      this.form.brief = detailData.brief
      this.form.canAppointmentPersonNum = detailData.canAppointmentPersonNum
      this.form.privateCourseId = detailData.id
      this.form.type = detailData.type
      this.text = '修改私教种类'
      // 支付方式
      for (var i=0; i<this.methodList.length; i++) {
        if (detailData[this.methodList[i].methods]) {
          this.methodList[i].selectList = detailData[this.methodList[i].methods]
          this.methodList[i].select = true
        }
      } 
    }
    this.saveData()
  },
  methods: {
    // 添加数据
    saveData () {
      let that = this
      that.prepiadList = []
      that.timesList = []
      that.detelimitList = []
      that.packageList = []
      vipCardKindList().then(function (res){
        let list = res.data.record
        for (var a=0; a<list.length; a++) {
          if (list[a].status == '1') {
            list.splice(a,1)
            a--
          }
        }
        for (var i=0; i<list.length; i++) {
          if (list[i].vipCardType == 'PREPAID') {
            that.prepiadList.push(list[i])
          }
          if (list[i].vipCardType == 'TIMES') {
            that.timesList.push(list[i])
          }
          if (list[i].vipCardType == 'DATELIMIT') {
            that.detelimitList.push(list[i]) // 弃用
          }
          if (list[i].vipCardType == 'PACKAGE') {
            that.packageList.push(list[i])
          }
        }
        for (var j=0; j<that.methodList.length; j++) {
          if (that.methodList[j].methods == 'prepaidPayList') {
            that.methodList[j].list = that.prepiadList
          }
          if (that.methodList[j].methods == 'timesPayList') {
            that.methodList[j].list = that.timesList
          }
          if (that.methodList[j].methods == 'packagePayList') {
            that.methodList[j].list = that.packageList
          }
        }
      })
    },
    // 增加私教课
    async submit (ruleForm) {
      if (this.form.generalPrice || this.form.generalPrice == 0) {
        this.form.generalPrice=this.form.generalPrice.toString()
      }
      if (this.form.vipPrice || this.form.vipPrice == 0) {
        this.form.vipPrice=this.form.vipPrice.toString()
      }
      // this.blur1(this.form.generalPrice)
      // this.blur2(this.form.vipPrice)
      this.blur3(this.form.generalPrice,this.form.vipPrice)
      this.checkPayway()
      let self = this
      this.$refs[ruleForm].validate((valid) => {
        if (valid) {
          if (!this.modify) {
            if (this.errorTips1 || this.errorTips2 || this.errorTips3) {
              return
            }
            let payList = this.methodList
            for (var i=0; i<payList.length; i++) {
              if (payList[i].selectList.length > 0) {
                this.form[payList[i].methods] = payList[i].selectList
              }
            }
            // console.log('this.form',this.form)
            addsession(this.form).then(function (res) {
              if (isApiResponseOk(res)) {
                self.$message({
                  showClose: true,
                  message: '增加成功',
                  type: 'success'
                })
                // console.log('this.form',this.form)
                self.$router.push({name: 'privatekind'})
              } else {
                self.$message({
                  showClose: true,
                  message: res.errMsg,
                  type: 'warning'
                })
              }  
            })
          } else {
            if (this.errorTips1 || this.errorTips2 || this.errorTips3) {
              return
            }
            let payList = this.methodList
            for (var i=0; i<payList.length; i++) {
              if (payList[i].selectList.length > 0) {
                this.form[payList[i].methods] = payList[i].selectList
              }
            }
            // console.log('this.form',this.form)
            updatesession(this.form.privateCourseId, this.form).then(function (res) {
              if (isApiResponseOk(res)) {
                self.$message({
                  showClose: true,
                  message: '修改成功',
                  type: 'success'
                })
                self.$router.push({name: 'privatekind'})
              }
              else {
                self.$message({
                  showClose: true,
                  message: res.errMsg,
                  type: 'warning'
                })
              }  
            })
          }
        }
      })
    },
    focus (str) {
      this.inputFocus = str;
    },
    blur () {
      this.inputFocus = ''
    },
    blur1 (item) {
      this.inputFocus = ''
      if (item === '') {
        this.errorTips1 = '价格不能为空'
      } else {
        if (isNaN(item) || item < 0) {
          this.errorTips1 = '价格为正整数'
        } else {
          this.errorTips1 = ''
        }
      }
    },
    blur2 (item) {
      this.inputFocus = ''
      if (item === '') {
        this.errorTips2 = '价格不能为空'
      } else {
        if (isNaN(item) || item < 0) {
          this.errorTips2 = '价格为正整数'
        } else {
          this.errorTips2 = ''
        }
      }
    },
    blur3 (m,n) {
      // console.log(m,n)
      if (!m && !n) {
        // this.errorTips2 = '课程价格不能都为空'
        this.redBorder = true
        this.$message({
          showClose: true,
          message: '课程价格不能都为空',
          type: 'warning'
        })
      } else {
        this.redBorder = false
        if (isNaN(m) || m < 0) {
          this.errorTips1 = '课程价格为正整数'
        } else {
          this.errorTips1 = ''
        }
        if (isNaN(n) || n < 0) {
          this.errorTips2 = '课程价格为正整数'
        } else {
          this.errorTips2 = ''
        }
      }
    },
    toaddCard () {
      this.$router.push({name: 'addvipkind'})
    },
    checkPayway () {
      let num = 0
      for (var i=0; i<this.methodList.length; i++) {
        if (!this.methodList[i].select || this.methodList[i].selectList.length == 0) {
          num++
        }
      }
      if (num == 4) {
        this.errorTips3 = '请选择支付方式'
      } else {
        this.errorTips3 = ''
      }
    },
    cancle () {
      this.$router.push({name: 'privatekind'})
    },
    // 返回到私教课程页面
    goback () {
      this.$router.push({name: 'privatekind'})
    },
    handleCheckedPayment (val) {
      // console.log('val',val)
    },
    handleChecked (str,obj) {
      // console.log(str,obj)
      if (!str) {
        // console.log('简历这里')
        for (var i=0; i<this.methodList.length; i++) {
          if (obj.methods == this.methodList[i].methods) {
            this.methodList[i].selectList = []
          }
        }
      } else {
        if (obj.methods == 'otherPayList') {
          this.methodList[3].selectList = ['WXPAY']
        }
      }
    }
  }
}
</script>
<style lang="sass" scoped>
#gymbranch.addprivatekind
    .el-form
        .el-form-item
          background: #FBFBFC;
          border: 1px solid #EAECEF;
          margin: 0.2rem 0;
          padding-left: 0.2rem;
          position: relative;
          height: 0.5rem;
          // overflow: hidden;
    .btn
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        display: flex;
        justify-content: center;
        .submit, .cancle
            background: #20C6BA;
            border-radius: .04rem;
            color: #fff;
            font-size: .14rem;
            width: 1.2rem;
            height: .44rem;
            line-height: .44rem;
            color: #fff;
            text-align: center;
            margin-top: .3rem;
            margin-bottom: .5rem;
            cursor: pointer;
        .cancle
          background: #C0C4CC;
          margin-right: 0.3rem;
    .top
        margin-bottom: .2rem;
        display: flex;
        align-items: center;
        .fundInfo
            font-size: .17rem;
            color: #8597A1;
            cursor: pointer;
        .branch
            font-size: .24rem;
            font-family: PingFangSC-Semibold;
            color: #263238;
        img
            width: .16rem;
            margin-left: .12rem;
            margin-right: .2rem;  
</style>
