  margin-right: 0.3rem;
  line-height: 0.3rem;
}

.addprivatekind .payWay .el-radio__label {
    font-size: .14rem !important;
    color: #263238;
}


.addprivatekind .el-form-item__label:before{
  content: ''!important;
}
.addprivatekind  .noPos .el-form-item__label:after{
  content: '';
}
.addprivatekind .el-form-item__label:after{
  content: '*';
  color: #f56c6c;
  margin-right: 4px;
  position: relative;
  top: 2px;
  left: 5px;
}
.addprivatekind .errorTip{
  position: absolute;
  top: 0.38rem;
  right: 0.2rem;
  color: #f56c6c;
  font-size: 0.12rem;
}
.addprivatekind .errorTips3{
  position: absolute;
  top: 0.4rem;
  left: 0rem;
  color: #f56c6c;
  font-size: 0.12rem;
}
/* .addprivatekind .el-form-item__error {
    color: #f56c6c;
    font-size: 12px;
    line-height: 1;
    padding-top: 4px;
    position: absolute;
    top: 0;
    left: 110%;
    display: inline-block;
    white-space: nowrap;
    line-height: 0.4rem;
} */
.addprivatekind .el-form-item__error{
  text-align: right;
