  padding-right: 0.2rem;
  top: 0.45rem;
  white-space: nowrap;
}
.addprivatekind .flexContent .el-form-item__error{
  text-align: left;
}
.addprivatekind .el-select .el-input .el-select__caret {
  position: absolute;
  top: -12px;
