  height: 40px;
}
.addprivatekind .el-select-dropdown.el-popper {
  left: 0!important;
  left: 7.2rem!important;
}
.addprivatekind .inputFocus{
  border: 2px solid #20C6BA!important;
}
.addprivatekind .redBorder{
  border: 2px solid #FC7563!important;
}
.addprivatekind .el-radio__input{
  width: 16px;
  height: 16px;
  background: rgba(233,236,239,0.30);
  border: 1px solid #E9ECEF;
  border-radius: 50%;
}
.addprivatekind .el-radio__input .el-radio__inner{
  width: 8px;
  height: 8px;
  border: 0;
  background: none;
  border-radius: 50%;
  position: relative;
  left: 3px;
  top: -1px;
}
.el-checkbox__input.is-checked .el-checkbox__inner, .el-checkbox__input.is-indeterminate .el-checkbox__inner {
    background: #20C6BA!important;
    border-color: #20C6BA!important;
}
.el-checkbox__input.is-checked+.el-checkbox__label{
  color: #606266!important;
}
#gymbranch.addprivatekind .el-input .el-input__icon.el-icon-arrow-up{
  line-height: inherit!important;
}
.addprivatekind .el-radio__input.is-checked .el-radio__inner::after {
  transform: scale(0);
}
.addprivatekind ::-webkit-scrollbar{
  width: 4px;  
  height: 16px;  
  background-color: #dcdfe6;
}
.addprivatekind ::-webkit-track{
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);  
  border-radius: 4px;  
  background-color: #fff;  
}
.addprivatekind ::-webkit-thumb{
  border-radius: 10px;  
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);  
  background-color: #fff;
}
@media screen and (max-width: 1280px) {
  /* #gymbranch.addprivatekind .el-input .el-input__inner{
    position: relative;
    top: -0.05rem;
  } */
  .addprivatekind .el-select .el-input .el-select__caret {
    position: absolute;
    top: -14px;
    /* left: 0.2rem; */
    height: 40px;
  }
}
