}
@media screen and (min-width: 1440px) and (max-width: 1680px) {
  /* #gymbranch.addprivatekind .el-input .el-input__inner{
    position: relative;
    top: -0.02rem;
  } */
}
@media screen and (min-width: 1681px) and (max-width: 1920px) {
  /* #gymbranch.addprivatekind .el-input .el-input__inner{
    position: relative;
    top: 0.04rem;
  } */
  .addprivatekind .el-select .el-input .el-select__caret {
    position: absolute;
    top: -9px;
    /* left: 0.2rem; */
    height: 40px;
  }
}
